import React, {useState} from 'react';
import ReactPlayer from 'react-player'

import logo from './express.png';
import './App.css';

function App() {
  const tracks = [
  ]
  const [count, setCount] = useState(0);
  const current = tracks[count]
  const next = () => {
    if (count === tracks.length - 1 ){
      return
    }
    setCount(count + 1)
  }
  const prev = () => {
    if (count === 0 ){
      return
    }
    setCount(count - 1)
  }
  const showTracks = tracks.length > 0
  const hide_prev = count === 0
  const hide_next = count === tracks.length - 1
  return (
    <div className="App">
      <header className="App-header">
        <div className="App-logo">
          <div>
          <img src={logo} className="logo" alt="logo" />
          </div>
        </div>
        {showTracks &&
        <div className="tracks-wrap">
          <div>
            <p># {current.id}</p>
            <p>{current.name}</p>
          </div> 
          <div className="buttons-wrap">
            <button onClick={prev} className={(hide_prev && "hide") || ""}>
              <p className="button-text">Prev</p>
            </button>
            <button onClick={next} className={(hide_next && "hide") || ""}>
              <p className="button-text">Next</p>
            </button>
          </div>
          <div className="control-wrap">
            <div className="control-box">
              <ReactPlayer 
                url={current.url}
                controls={true}
                width='100%'
                height='100%'
              />
            </div>
          </div>
        </div>
        }
      </header>
    </div>
  );
}

export default App;
